.counterBlock {
    display: flex;
    align-items: center;
    margin-left: 15px;
}

.incrementButton {
    all: unset;
    background: transparent;
    border: 2px solid white;
    padding: 15px 91px;
    border-radius: 50px;
    cursor: pointer;
    display: inline-block;
    font-weight: 700;
    position: relative;
    transition: all 300ms ease;
}

.incrementButton:enabled:before, .incrementButton:after {
    content: "";
    display: block;
    position: absolute;
    transition: all 300ms ease;
    opacity: 0;
}

.incrementButton:enabled:before {
    height: 7px;
    width: 7px;
    background: transparent;
    border-right: 2px solid;
    border-top: 2px solid;
    right: 30px;
    top: 21px;
    transform: rotate(45deg);
}

.incrementButton:enabled:after {
    background: white;
    height: 2px;
    width: 50px;
    left: 0;
    top: 1.49em;
}

.incrementButton:enabled:hover span {
    transform: translateX(-10px);
}

.incrementButton:enabled:hover:before {
    opacity: 1;
}

.incrementButton:enabled:hover:after {
    width: 14px;
    opacity: 1;
    transform: translateX(160px);
}

.incrementButton:disabled {
    all: unset;
    background: transparent;
    border: 2px solid rgba(222, 239, 231, 0.5);
    color: rgba(222, 239, 231, 0.7);
    padding: 15px 91px;
    border-radius: 50px;
    cursor: pointer;
    display: inline-block;
    font-weight: 700;
    position: relative;
}

.resetButton {
    all: unset;
    margin-left: 10px;
    background: transparent;
    border: 2px solid white;
    padding: 5px 10px;
    border-radius: 50px;
    cursor: pointer;
    display: inline-block;
    font-weight: 300;
    position: relative;
    transition: all 300ms ease;
}

.resetButton:disabled {
    all: unset;
    margin-left: 10px;
    background: transparent;
    border: 2px solid rgba(222, 239, 231, 0.5);
    color: rgba(222, 239, 231, 0.7);
    padding: 5px 10px;
    border-radius: 50px;
    cursor: pointer;
    display: inline-block;
    font-weight: 300;
    position: relative;
}